html {
  scroll-behavior: smooth; }

body {
  font-family: sans-serif;
  color: $second-color;
  margin: 0px;
  background: #125778;
 }  // background: #ffffca

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }


.clearfix {
  display: inline-block; }

/* start commented backslash hack \*/

* html .clearfix {
  height: 1%; }

.clearfix {
  display: block; }


.container {
  margin: 0 auto;
  max-width: 1170px; }

header {
  box-sizing: border-box;
  width: 100%;
  color: white;
  min-height: 50px;
  padding: 0 10px; }



.nav {
  @include r(900) {
    display: none; }
  ul {
    box-sizing: border-box;
    list-style: none;
    overflow: hidden;
    padding: 15px 0px 0px 0px;
    margin: 0;
    @include r(900) {
      width: 100%;
      text-align: center; } }
  li {
    float: right;
    a {
      display: block;
      text-decoration: none;
      text-transform: uppercase;
      padding: 0px 14px;
      color: white;
      font-weight: bold;
      &:hover {
        color: #12ec12; } } } }

.welcome-section {
  box-sizing: border-box;
  min-height: 700px;
  color: #fff;
  background: #125778;
  background: -webkit-linear-gradient(#125778, #2098D1);
  background: -o-linear-gradient(#125778, #2098D1);
  background: linear-gradient(#125778, #2098D1);
  box-sizing: border-box;
  text-align: center;
  font-size: 25px;
  padding: 150px 15px 0px 15px;
  @include r(500) {
    padding: 150px 0px 0px 0px; }
  &__title {
    margin: 10px 0px;
    font-size: 36px;
    line-height: 1.1;
    &:after {
      content: "";
      display: block;
      margin: 5px auto 0;
      background-color: #c6e9ef;
      width: 150px;
      height: 3px; } }
  &__description {
    padding-bottom: 60px; }
  &__button {
    background: #045f8c;
    font-weight: bold;
    color: white;
    text-decoration: none;
    font-size: 14px;
    padding: 12px 16px 12px 16px;
    display: inline-block;
    border-style: none;
    margin-bottom: 50px;
    &:hover {
      text-decoration: none;
      color: #045f8c;
      background: #c6e9ef; } } }

.section-title {
  margin: 0;
  padding: 30px 0px 30px 0px;
  text-align: center;
  color: $second-color;
  text-transform: uppercase;
  font-weight: 500;
  font-family: arial;
  font-size: 24px; }

.portfolio-section {
  background: #c6e9ef;
  min-height: 500px;
  padding-bottom: 60px;
  overflow: auto; }

.portfolio-col {
  text-align: center;
  padding: 0 1% 30px 1%;
  float: left;
  width: 33.33%;
  min-height: 430px;
  @include r(1037) {
      width: 50%; }
  @include r(700) {
      width: 100%; }
  a {
    position: relative;
    height: 100%;
    display: inline-block; }
  &__img {
    height: 240px;
    width: auto;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
    @include r(450) {
      width: 100%; } }
  &__title {
    font-family: arial;
    font-weight: 500;
    line-height: 1.1;
    font-size: 24px;
    text-transform: uppercase;
    margin: 20px 0 10px 0;
    a {
      text-decoration: none;
      color: $second-color;
      &:hover {
        color: #12ec12; } } }
  &__animation {
    -webkit-transition: .2s;
    transition: .2s;
    &:hover {
      -webkit-transform: scale(1.1);
      transform: scale(1.1);
      color: #000;
      outline-color: #b0b0b0; }
    &:after {
      display: inline-block; } }
  &__description {
    margin: 2%;
    font-size: 16px;
    font-family: verdana;
    height: 90px;
    overflow: auto; } }

.icon-new-tab:after {
  content: "\ea7e";
  color: #c6e9ef;
  position: absolute;
  left: 3px;
  bottom: 3px;
  z-index: 1;
  font-size: 40px; }


.icon-new-tab:hover:after {
    color: #12ec12; }


.about-section {
  min-height: 450px;
  background: #f8f8f8;
  p {
    text-align: center;
    font-family: roboto;
    font-size: 27px;
    padding: 20px 15px 40px 15px;
    @include r(700) {
      font-size: 20px;
      padding: 0px 15px 40px 15px; } } }



.contact-section {
  background: #c6e9ef;
  min-height: 400px; }

.contact {
  text-align: center;
  margin: 0 auto 50px auto;
  max-width: 500px;
  padding: 0 2%;
  &__description {
    color: #5a90ba;
    font-size: 20px;
    padding-bottom: 20px; }

  &__btn {
    display: block;
    margin: 10px 0px 10px 0px;
    padding: 10px 16px;
    font-size: 18px;
    line-height: 1.3;
    border-radius: 6px;
    color: #fff;
    background-color: #3e3e12;
    text-decoration: none;
    &:hover {
      background-color: #12ec12; } } }

.btn-blue {
  background-color: blue; }

.btn-green {
  background: green; }


footer {
  background: #2098D1;
  text-align: right;
  color: #fff;
  font-size: 14px;
  padding: 20px; }

