html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  line-height: 1.4;
}

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?ecg8pc");
  src: url("../fonts/icomoon.eot?ecg8pc#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?ecg8pc") format("truetype"), url("../fonts/icomoon.woff?ecg8pc") format("woff"), url("../fonts/icomoon.svg?ecg8pc#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font endering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: sans-serif;
  color: #5a90ba;
  margin: 0px;
  background: #125778;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
}

header {
  box-sizing: border-box;
  width: 100%;
  color: white;
  min-height: 50px;
  padding: 0 10px;
}

@media only screen and (max-width: 900px) {
  .nav {
    display: none;
  }
}

.nav ul {
  box-sizing: border-box;
  list-style: none;
  overflow: hidden;
  padding: 15px 0px 0px 0px;
  margin: 0;
}

@media only screen and (max-width: 900px) {
  .nav ul {
    width: 100%;
    text-align: center;
  }
}

.nav li {
  float: right;
}

.nav li a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0px 14px;
  color: white;
  font-weight: bold;
}

.nav li a:hover {
  color: #12ec12;
}

.welcome-section {
  box-sizing: border-box;
  min-height: 700px;
  color: #fff;
  background: #125778;
  background: -webkit-linear-gradient(#125778, #2098D1);
  background: -o-linear-gradient(#125778, #2098D1);
  background: linear-gradient(#125778, #2098D1);
  box-sizing: border-box;
  text-align: center;
  font-size: 25px;
  padding: 150px 15px 0px 15px;
}

@media only screen and (max-width: 500px) {
  .welcome-section {
    padding: 150px 0px 0px 0px;
  }
}

.welcome-section__title {
  margin: 10px 0px;
  font-size: 36px;
  line-height: 1.1;
}

.welcome-section__title:after {
  content: "";
  display: block;
  margin: 5px auto 0;
  background-color: #c6e9ef;
  width: 150px;
  height: 3px;
}

.welcome-section__description {
  padding-bottom: 60px;
}

.welcome-section__button {
  background: #045f8c;
  font-weight: bold;
  color: white;
  text-decoration: none;
  font-size: 14px;
  padding: 12px 16px 12px 16px;
  display: inline-block;
  border-style: none;
  margin-bottom: 50px;
}

.welcome-section__button:hover {
  text-decoration: none;
  color: #045f8c;
  background: #c6e9ef;
}

.section-title {
  margin: 0;
  padding: 30px 0px 30px 0px;
  text-align: center;
  color: #5a90ba;
  text-transform: uppercase;
  font-weight: 500;
  font-family: arial;
  font-size: 24px;
}

.portfolio-section {
  background: #c6e9ef;
  min-height: 500px;
  padding-bottom: 60px;
  overflow: auto;
}

.portfolio-col {
  text-align: center;
  padding: 0 1% 30px 1%;
  float: left;
  width: 33.33%;
  min-height: 430px;
}

@media only screen and (max-width: 1037px) {
  .portfolio-col {
    width: 50%;
  }
}

@media only screen and (max-width: 700px) {
  .portfolio-col {
    width: 100%;
  }
}

.portfolio-col a {
  position: relative;
  height: 100%;
  display: inline-block;
}

.portfolio-col__img {
  height: 240px;
  width: auto;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
}

@media only screen and (max-width: 450px) {
  .portfolio-col__img {
    width: 100%;
  }
}

.portfolio-col__title {
  font-family: arial;
  font-weight: 500;
  line-height: 1.1;
  font-size: 24px;
  text-transform: uppercase;
  margin: 20px 0 10px 0;
}

.portfolio-col__title a {
  text-decoration: none;
  color: #5a90ba;
}

.portfolio-col__title a:hover {
  color: #12ec12;
}

.portfolio-col__animation {
  -webkit-transition: .2s;
  transition: .2s;
}

.portfolio-col__animation:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  color: #000;
  outline-color: #b0b0b0;
}

.portfolio-col__animation:after {
  display: inline-block;
}

.portfolio-col__description {
  margin: 2%;
  font-size: 16px;
  font-family: verdana;
  height: 90px;
  overflow: auto;
}

.icon-new-tab:after {
  content: "\ea7e";
  color: #c6e9ef;
  position: absolute;
  left: 3px;
  bottom: 3px;
  z-index: 1;
  font-size: 40px;
}

.icon-new-tab:hover:after {
  color: #12ec12;
}

.about-section {
  min-height: 450px;
  background: #f8f8f8;
}

.about-section p {
  text-align: center;
  font-family: roboto;
  font-size: 27px;
  padding: 20px 15px 40px 15px;
}

@media only screen and (max-width: 700px) {
  .about-section p {
    font-size: 20px;
    padding: 0px 15px 40px 15px;
  }
}

.contact-section {
  background: #c6e9ef;
  min-height: 400px;
}

.contact {
  text-align: center;
  margin: 0 auto 50px auto;
  max-width: 500px;
  padding: 0 2%;
}

.contact__description {
  color: #5a90ba;
  font-size: 20px;
  padding-bottom: 20px;
}

.contact__btn {
  display: block;
  margin: 10px 0px 10px 0px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.3;
  border-radius: 6px;
  color: #fff;
  background-color: #3e3e12;
  text-decoration: none;
}

.contact__btn:hover {
  background-color: #12ec12;
}

.btn-blue {
  background-color: blue;
}

.btn-green {
  background: green;
}

footer {
  background: #2098D1;
  text-align: right;
  color: #fff;
  font-size: 14px;
  padding: 20px;
}

.sidenav {
  position: fixed;
  width: 0px;
  height: 100%;
  background: #5a90ba;
  text-align: center;
  z-index: 1;
  top: 0;
  right: 0;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

@media only screen and (min-width: 901px) {
  .sidenav {
    display: none;
  }
}

.sidenav ul {
  list-style: none;
}

.sidenav li a {
  color: #fff;
  padding: 15px 10px;
  display: inline-block;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.3s;
}

.sidenav .closebtn {
  position: absolute;
  top: 5px;
  right: 45%;
  font-size: 50px;
  margin-left: 50px;
  text-decoration: none;
  color: #fff;
}

.sidenav .closebtn:hover {
  color: #12ec12;
}

.toggle-btn {
  position: fixed;
  right: 20px;
  top: 15px;
  cursor: pointer;
}

.toggle-btn span {
  display: block;
  width: 30px;
  height: 5px;
  background: #5a90ba;
  margin: 5px 0px;
}

.sidenav ul li a:hover {
  color: #12ec12;
}
