
.sidenav {
  position: fixed;
  width: 0px;
  height: 100%;
  background: $second-color;
  text-align: center;
  z-index: 1;
  top: 0;
  right: 0;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
  @include rmin(901) {
    display: none; }
  ul {
    list-style: none; }
  li a {
    color: #fff;
    padding: 15px 10px;
    display: inline-block;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    transition: 0.3s; } }




.sidenav .closebtn {
  position: absolute;
  top: 5px;
  right: 45%;
  font-size: 50px;
  margin-left: 50px;
  text-decoration: none;
  color: #fff;
  &:hover {
    color: $active-color; } }



.toggle-btn {
  position: fixed;
  right: 20px;
  top: 15px;
  cursor: pointer; }
.toggle-btn span {
  display: block;
  width: 30px;
  height: 5px;
  background: #5a90ba;
  margin: 5px 0px; }



.sidenav ul li a:hover {
  color: $active-color; }
